@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin fixed($t, $l, $b, $r) {
    position: fixed;
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }    
}
@mixin left($l) {
  left: $l;
}
@mixin right($r) {
  right: $r;
}
@mixin rotate($rotate) { 
  transform: rotate($rotate);
}
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin text-right {
    text-align: end;
}
@mixin text-left {
    text-align: start;
}

