.list-cart-items::-webkit-scrollbar-track {
	box-shadow: inset 0 0 4px var(--miscellaneous-color);
	-webkit-box-shadow: inset 0 0 var(--miscellaneous-color);
	background-color: var(--miscellaneous-color);
	border-radius: 5px;
}
.list-cart-items::-webkit-scrollbar {
	width: 4px;
	background-color: var(--miscellaneous-color);
	border-radius: 5px;
}
.list-cart-items::-webkit-scrollbar-thumb {
	background-color: var(--primary-color);
	border-radius: 5px;
}
.header-icon--cart {
	template {
		@include ab(100%,n,n,0); 
		display: block;
		width: 100%;
		height: 100%;
	}
	>a.active ~ cart-items {
		opacity: 1;
		visibility: visible;
	}
}
cart-items {
	opacity: 0;
	visibility: hidden;
	transition: 0.25s all ease-out;
	.cart-empty {
		margin: 0;
	}
}
.cart-loading {
	display: flex;
	justify-content: center;
}
.form-cart-section {
	width: 420px;
	padding: 25px;
	@include padding-end(20px);
	background: var(--background-color);
	box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
	.cart-empty-inner {
		display: flex;
		justify-content: center;
		align-items: center;
		svg {
			width: 26px;
			height: 26px;
			@include margin-end(8px);
		}
		.h3 {
			font-size: 20px;
			font-weight: 600;
		}
	}
	.list-cart-items {
		max-height: 320px;
		overflow: hidden;
		overflow-y: auto;
	}
	.cart-item:not(:last-child) {
		padding-bottom: 20px;
		margin-bottom: 20px;
		border-bottom: 1px solid var(--miscellaneous-color);
	}
	.item-inner {
		display: flex;
		align-items: center;
		justify-content: space-between;
		>div {
			display: block;
		}
	}
	.item-image {
		flex: 0 0 80px;
		max-width: 80px;
		@include margin-end(15px);
		a {
			display: block;
		}
	}
	.item-information {
		flex: 1;
	}
	.item-quantity {
		@include margin-start(10px);
		p {
			margin: 0;
			padding: 8.5px 5px;
			min-width: 40px;
			text-align: center;
			border-radius: var(--border-radius);
			border: 1px solid var(--miscellaneous-color);
		}
	}
	.item-remove {
		@include margin-start(10px);
		cart-remove-button {
			@include padding-end(8px);
			display: block;
			&:hover {
				a {
					opacity: 0.9;
				}
			}
		}
		a {
			padding: 5px;
			transition: 0.2s all ease-in-out;
			svg {
				width: 16px;
				height: 16px;
			}
		}
		.loading {
			.icon-remove {
				display: none;
			}
		}
	}
}
.mini-cart-footer {
	margin-top: 20px;
	padding-top: 20px;
	border-top: 1px solid var(--miscellaneous-color);
	.totals {
		margin-bottom: 20px;
		align-items: center;
	}
	.totals__subtotal-value {
		margin: 0;
		font-size: 20px;
		font-weight: 500;
		color: var(--primary-color);
	}
	.cart-buttons {
		.btn {
			display: flex;
			align-items: center;
			text-align: center;
			justify-content: center;
			width: 100%;
			margin-top: 12px;
		}
	}
}